.notfoundpage {
  display: grid;
  place-items: center;
  width: 82.2%;
  background-color: #f1f1f1;
}

.notfoundpage__logo {
  background: no-repeat
    url("//www.gstatic.com/youtube/img/branding/youtubelogo/1x/youtubelogo_30.png");
  width: 125px;
  height: 30px;
  cursor: pointer;
  display: inline-block;
}

.notfoundpage__container {
  display: flex;
  align-items: center;
}
